.AghoriDocxDiv{
    margin-top: 0;
    background: rgb(0, 0, 0);
    padding: 10px 5px;
}

.marquee-container {
    width: 100%;
    overflow: hidden;
  }
  
  .marquee-content {
    white-space: nowrap;
    animation: marquee 60s linear infinite;

        font-size: 72px;
        background: -webkit-linear-gradient(#eee, #2113dd ,grey);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      
  }
  
  @keyframes marquee {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  

